.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 1rem;
  border-radius: 20px;
  margin-bottom: 2rem;
}
.alerta.error {
  background-color: #FF5733;
}
.alerta.exito {
  background-color: green;
}

header {
  min-height: 2.5rem;
}

header .header-container {
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 1024px) {
  header .header-container {
    padding: 0 10rem;
  }
}

header .header-container .logo,
header .header-container img {
  width: 8rem;
}
@media (min-width: 1024px) {
  header .header-container .logo,
  header .header-container img {
    width: 12rem;
  }
}

header .header-container .burger-m-container {
  height: 4.5rem;
  border: 1px solid #e7e7e7;
  border-radius: 50%;
}
@media (min-width: 768px) {
  header .header-container .burger-m-container {
    display: none;
  }
}

header .header-container .burger-m-container .burger-m {
  padding: 7px;
  height: 100%;
}

header .header-container .nav-list-container {
  display: none;
}
@media (min-width: 768px) {
  header .header-container .nav-list-container {
    display: flex;
  }
}

@keyframes animacionMenu {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
header .header-container .activate {
  display: flex;
  flex-flow: column nowrap;
  flex: 100%;
  animation: animacionMenu 350ms ease-in-out both;
}
@media (min-width: 768px) {
  header .header-container .activate {
    flex: 0;
    flex-flow: row;
  }
}

header .header-container .activate .nav-list,
header .header-container .nav-list-container .nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  list-style: none;
  height: 100%;
}
@media (min-width: 768px) {
  header .header-container .activate .nav-list,
  header .header-container .nav-list-container .nav-list {
    flex-direction: row;
  }
}
header .header-container .activate .nav-list li,
header .header-container .nav-list-container .nav-list li {
  padding: 0.6rem 2rem;
}
header .header-container .activate .nav-list li a,
header .header-container .nav-list-container .nav-list li a {
  text-decoration: none;
  color: #7e7e7e;
  font-size: 1.8rem;
}
header .header-container .activate .nav-list li a:hover,
header .header-container .nav-list-container .nav-list li a:hover {
  color: black;
}

footer {
  margin-top: 4rem;
  position: relative;
  height: 6rem;
  background-color: #f7f7f7;
  border-top: 1px dotted #a5a4a4;
}

footer .footer-contain {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

footer .footer-contain .Copyright {
  height: 100%;
  padding-left: 1.5rem;
  font-size: 1.7rem;
  display: flex;
  align-items: center;
}

footer .footer-contain .icons-container {
  margin-right: 2rem;
}

footer .footer-contain .icons-container a {
  text-decoration: none;
}

footer .footer-contain .icons-container a img {
  height: 3rem;
  margin-left: 1rem;
  padding-top: 0.3rem;
}

main {
  min-height: calc(100vh - 20rem);
}
@media (min-width: 1024px) {
  main {
    min-height: 50rem;
  }
}

main .image-container {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/home.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  height: 260px;
  width: 100%;
}
@media (min-width: 1024px) {
  main .image-container {
    height: 500px;
  }
}

main .first-section-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main .first-section-container h1 {
  color: azure;
  text-align: center;
  font-weight: bold;
}

main .first-section-container .search {
  margin-top: 2rem;
  height: 4rem;
}

main .first-section-container .search form {
  position: relative;
}

main .first-section-container .search form input {
  padding: 0 20px;
  outline: none;
  height: 35px;
  border-radius: 5px;
  font-size: 16px;
  border: none;
  padding-left: 50px;
}
@media (min-width: 768px) {
  main .first-section-container .search form input {
    width: 600px;
  }
}

main .first-section-container .search form button {
  border: none;
  position: absolute;
  width: 4rem;
  height: 3.5rem;
  background-color: #007aff;
  border-radius: 5px 0px 0px 5px;
  cursor: pointer;
}

main .first-section-container .search form button i {
  position: absolute;
  font-size: 16px;
  top: 50%;
  transform: translateY(-50%);
  left: 12px;
  color: #e8e8e8;
}

main .second-section-container {
  height: auto;
  width: auto;
  margin: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

main .second-section-container h2 {
  text-align: center;
  margin-bottom: 3rem;
}

main .second-section-container .cards-container {
  display: grid;
  gap: 4rem;
}
@media (min-width: 768px) {
  main .second-section-container .cards-container {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    width: 73rem;
    justify-content: center;
  }
}

main .second-section-container .cards-container .card-container {
  height: auto;
  width: 22rem;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border: 1px solid #e7e7e7;
  border-radius: 5px;
}

main .second-section-container .cards-container .card-container img {
  width: 100%;
  border-radius: 5px;
}

main .second-section-container .cards-container .card-container .title {
  font-weight: bold;
}

main .second-section-container .cards-container .card-container .price {
  font-weight: bold;
  color: green;
}

main .second-section-container .cards-container .card-container button {
  background-color: #007aff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

main .second-section-container .cards-container .card-container button a {
  display: block;
  padding: 1rem;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: white;
}

@media (min-width: 1024px) {
  main .second-section-container .grid-ajuste {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 4rem;
  }
}

.nosotros h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.nosotros .contenedor-nosotros {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .nosotros .contenedor-nosotros {
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
}

.nosotros .contenedor-nosotros .contenedor-imagen {
  width: 25rem;
  height: 20rem;
  align-self: center;
}
@media (min-width: 1024px) {
  .nosotros .contenedor-nosotros .contenedor-imagen {
    width: 35rem;
    height: 30rem;
  }
}

.nosotros .contenedor-nosotros .contenedor-imagen img {
  width: 100%;
  height: 100%;
}

.nosotros .contenedor-nosotros p {
  padding-left: 2.4rem;
  padding-right: 2.4rem;
  line-height: 2rem;
}
@media (min-width: 768px) {
  .nosotros .contenedor-nosotros p {
    width: 40rem;
  }
}

main .form-section h2 {
  margin-bottom: 2rem;
  font-weight: 800;
}

main .form-section {
  max-width: 50rem;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 10rem;
}

main .form-section .form-section-flex-container {
  display: flex;
  max-width: 65rem;
  margin-right: auto;
  margin-left: auto;
  flex-direction: column;
  align-items: center;
}

main .form-section .form-section-flex-container .contact {
  width: 100%;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
}

main .form-section .form-section-flex-container .contact .field-label {
  font-size: 1.4rem;
  line-height: 130%;
  font-weight: 500;
}

main .form-section .form-section-flex-container .contact label {
  display: block;
  margin-bottom: 1rem;
}

main .form-section .form-section-flex-container .contact .field-text {
  margin-top: 0px;
  min-height: 6rem;
  margin-bottom: 2rem;
  border-radius: 0.3rem;
  transition: border 200ms ease;
  display: block;
  width: 100%;
  height: 3.8rem;
  padding: 0.8rem 1.2rem;
  font-size: 1.4rem;
  line-height: 1.42857143;
  color: #333333;
  background-color: #ffffff;
  border: 1px solid #cccccc;
}

main .form-section .form-section-flex-container .contact .field-textarea {
  height: auto;
  min-height: 23rem;
  border-radius: 3px;
  transition: border 200ms ease;
  display: block;
  width: 100%;
  padding: 0.8rem 1.2rem;
  margin-bottom: 1rem;
  font-size: 1.4rem;
  line-height: 1.42857143;
  color: #333333;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  overflow: auto;
}

main .form-section .form-section-flex-container .contact .form-button {
  display: flex;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  justify-content: center;
  align-items: center;
  border-radius: 0.6rem;
  background-color: #007aff;
  font-size: 1.6rem;
  font-weight: 500;
  padding: 9px 15px;
  color: white;
  border: 0;
  line-height: inherit;
  text-decoration: none;
  cursor: pointer;
  margin-top: 2rem;
}

@media (max-width: 768px) {
  main .title-form-section {
    padding-right: 2rem;
    padding-left: 3.5rem;
  }
  main .form-section {
    padding-right: 2rem;
    padding-left: 2rem;
  }
}
#imagen {
  background-color: #fff;
  color: #333;
  display: block;
  font-size: 1.4rem;
  line-height: 1.42857143;
  padding: 0.8rem 1.2rem;
  transition: border 0.2s ease;
  width: 100%;
  margin-bottom: 2rem;
}

.imagen-small {
  width: 20rem;
}

main .props-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 2rem;
}
@media (min-width: 1024px) {
  main .props-container {
    width: 60%;
    margin: 0 auto;
  }
}

@media (min-width: 1024px) {
  main .props-container img {
    height: 35rem;
    width: 50rem;
    align-self: center;
  }
}

main .props-container .props-price {
  font-weight: bold;
  color: green;
}

.admin-main {
  min-height: 49.8rem;
}

.admin-main .admin-header-contain {
