.sign-in {
  max-width: 44rem;
  padding: 0 2rem;
  margin: 10rem auto;
  font-family: "Poppins", sans-serif;
}

.wrapper {
  width: 100%;
  background: #fff;
  border-radius: 5px;
  box-shadow: 0px 4px 10px 1px rgba(0, 0, 0, 0.1);
}

.wrapper .title {
  height: 9rem;
  background: #007aff;
  border-radius: 5px 5px 0 0;
  color: #fff;
  font-size: 3rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper form {
  padding: 1.5rem;
}

.wrapper form .row {
  height: 4.5rem;
  margin-bottom: 1.5rem;
  position: relative;
}

.wrapper form .row input {
  height: 100%;
  width: 100%;
  outline: none;
  padding-left: 6rem;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-size: 1.6rem;
  transition: all 0.3s ease;
}

form .row input:focus {
  border-color: #007aff;
  box-shadow: inset 0px 0px 2px 2px #007aff;
}

form .row input::placeholder {
  color: #999;
}

.wrapper form .row i {
  position: absolute;
  width: 4.7rem;
  height: 100%;
  color: #fff;
  font-size: 1.8rem;
  background: #007aff;
  border: 1px solid #007aff;
  border-radius: 5px 0 0 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrapper form .button input {
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  padding-left: 0px;
  background: #007aff;
  border: 1px solid #007aff;
  cursor: pointer;
}

form .button input:hover {
  background: #007aff;
}